<template>
  <div class="airdrop">
    <p class="text-16 text-white">Use DxDrop to airdrop your token to all your users with the cli</p>
    <div class="airdrop-content MT-30">
      <div v-click-outside="clickOutside" class="airdrop-type flex-row-between pointer MB-30" :class="{ 'type-show': typesShow }" @click="typesShow = !typesShow">
        <div class="airdrop-type__left text-left">
          <div class="text-14 text-999">Aindrop Type</div>
          <div class="text-18 text-white MT-10">{{ type == 1 ? 'Aindrop Tokens' : 'BNB' }}</div>
        </div>
        <el-icon color="#fff" :size="16">
          <caret-bottom />
        </el-icon>
        <transition name="el-zoom-in-top">
          <div v-show="typesShow" class="airdrop-type__list">
            <div class="airdrop-type__list-item" @click="toggleType(1)">Aindrop Type</div>
            <!-- <div class="airdrop-type__list-item" @click="toggleType(2)">BNB</div> -->
          </div>
        </transition>
      </div>
      <div class="airdrop-desc text-left text-16 text-999 MB-40">
        <div class="airdrop-desc__title text-18 text-white MB-10">Aindrop Instructions:</div>
        <div class="MT-5">- Airdrop tokens to as many users as desired</div>
        <div class="MT-5">- If you are running a sale make sure tokens are not airdropped</div>
        <div class="MT-5">- Enter your token address first</div>
        <div class="MT-5">- Enter a list of users to airdrop followed by amount(comma sepa</div>
      </div>
      <div class="text-14 text-999 MB-20">Airdrop Fees:{{ utils.formatEther(feeAmount) }} BNB</div>
      <transition name="el-zoom-in-top">
        <div v-show="type == 1" class="airdrop-type MB-40 text-left MB-30">
          <div class="text-14 text-999">Enter Token Address</div>
          <div class="flex-row-start MT-10 align-center">
            <input v-model.trim="tokenAddr" class="airdrop-type__input text-18 text-white flex-1" placeholder="Please Enter Token Address">
            <el-icon v-show="searchLoading" class="is-loading" color="#fff">
              <loading />
            </el-icon>
          </div>
        </div>
      </transition>
      <div v-show="type == 1" class="text-16 text-999 text-left">
        <span class="MR-5">Token:</span>
        <span class="text-red">{{ ERC20Info.symbol }}({{ ERC20Info.name || '--' }})</span>
      </div>
      <div class="text-16 text-999 text-left MT-10">
        <span class="MR-5">Balance:</span>
        <span v-if="type == 1" class="text-red">{{ utils.formatUnits(ERC20Info.balance, ERC20Info.decimals) }}</span>
        <span v-else class="text-red">{{ utils.formatEther(ERC20Info.balance) }}</span>
      </div>
      <div class="airdrop-list MT-20">
        <textarea v-model.trim="disListInfo" class="airdrop-list__textarea text-white text-16" placeholder="Enter Distribution List" />
      </div>
      <p class="text-red text-16 MT-10 MB-40">For best results we recommend you do a maximum of 500 Addresses at a time!</p>
      <div class="text-16 text-999">
        Total tokens being airdropped:
        <span class="text-red">{{ totalPrice }}</span>
      </div>
      <div class="flex-row-center MT-15">
        <el-button v-if="!isLogin" class="btn btn-line color-text MR-20" :loading="loginLoading" @click="loginHandler">Connect</el-button>
        <el-button v-else-if="isApprove == 2" class="btn btn-line color-text MR-20" :loading="approveLoading" @click="approveHandler">APPROVE</el-button>
        <el-button v-else-if="isApprove == 1" class="btn" :loading="airdropLoading" @click="airdropHandler">AIRDROP</el-button>
      </div>
    </div>
  </div>
</template>

<script>
/* global wallet */
import { CaretBottom, Loading } from '@element-plus/icons'
import { ElNotification } from 'element-plus'
import { debounce, Tips } from '@/libs/utils.js'
import { BigNumber, Contract, utils } from 'ethers'
import IERC20ABI from '@/api/abis/IERC20.json'

window.Contract = Contract
window.IERC20ABI = IERC20ABI
let contractERC20 = null
export default {
  components: {
    CaretBottom,
    Loading
  },
  data() {
    return {
      utils: utils,
      typesShow: false,
      // 当前选中类型
      type: 1,
      // 是否有登陆
      isLogin: false,
      // 是否有授权 0: 未知 1:已授权 2:未授权
      isApprove: 0,
      // 授权loading
      approveLoading: false,
      // 登陆loading
      loginLoading: false,
      feeAmount: BigNumber.from(0),
      // 合约地址
      tokenAddr: '',
      disListInfo: '',
      // 空投loading
      airdropLoading: false,
      searchLoading: false,
      ERC20Info: {
        name: '',
        symbol: '',
        decimals: 0,
        balance: BigNumber.from(0),
        allowance: BigNumber.from(0)
      }
    }
  },
  computed: {
    // 格式化列表
    disList() {
      let list = []
      const resList = []
      if (this.disListInfo) {
        list = this.disListInfo.split('\n')
      }
      for (let i = 0; i < list.length; i++) {
        const item = list[i]
        const arr = item.split(',')
        resList.push({
          addr: arr[0],
          price: arr[1]
        })
      }
      return resList
    },
    // 统计总金额
    totalPrice() {
      let totalPrice = 0
      this.disList.forEach((item) => {
        const price = item.price
        if (price && !isNaN(price)) {
          totalPrice += Number(price)
        }
      })
      return totalPrice
    }
  },
  watch: {
    tokenAddr: debounce(async function() {
      this.searchLoading = true
      // TODO 搜索接口
      try {
        const ERC20 = new Contract(this.tokenAddr, IERC20ABI, wallet.airDropABI.signer)
        const [name, symbol, decimals, balance, allowance] = await Promise.all([
          ERC20.name(),
          ERC20.symbol(),
          ERC20.decimals(),
          ERC20.balanceOf(wallet.accounts[0]),
          ERC20.allowance(wallet.accounts[0], wallet.airDropABI.address)
        ])
        this.ERC20Info = { name, symbol, decimals, balance }
        this.isApprove = allowance.isZero() ? 2 : 1
        contractERC20 = ERC20
      } catch (error) {
        console.error(error)
        this.ERC20Info = {
          name: '',
          symbol: '',
          decimals: 0,
          balance: BigNumber.from(0),
          allowance: BigNumber.from(0)
        }
        this.isApprove = 2
        contractERC20 = null
      }
      this.searchLoading = false
    }, 500)
  },
  created() {
    this.isLoginFn()
    this.$wallet.on('accountChange', () => {
      this.isLoginFn()
    })
  },
  methods: {
    // 是否登陆
    async isLoginFn() {
      if (this.$wallet.accounts[0]) {
        this.isLogin = true
        // 获取手续费
        await this.$wallet.initNetwork()
        this.$wallet.airDropABI.price().then((val) => {
          this.feeAmount = val
        })
      } else {
        this.isLogin = false
      }
    },
    // 获取BNB余额
    getBnbBalance() {
      wallet.scoutDogeABI.provider.getBalance(wallet.accounts[0]).then((data) => {
        console.log(data)
        this.ERC20Info = { name: 'BNB', balance: data }
      })
    },
    clickOutside() {
      this.typesShow = false
    },
    /**
     * 切换类型
     * @param {Number} type 当前选中类型 1:Aindrop Tokens 2:BNB
     */
    toggleType(type) {
      this.type = type
      this.ERC20Info = {
        name: '',
        symbol: '',
        decimals: 0,
        balance: BigNumber.from(0),
        allowance: BigNumber.from(0)
      }
      if (type === 2) {
        this.getBnbBalance()
      }
    },
    // 登陆
    loginHandler() {
      this.loginLoading = true
      this.$wallet
        .initNetwork()
        .then(() => {
          this.isLogin = true
        })
        .finally(() => {
          this.loginLoading = false
        })
    },
    // 授权
    async approveHandler() {
      // TODO 授权接口
      const bool = this.dataVerify()
      if (!bool) {
        return
      }
      this.approveLoading = true
      try {
        const res = await contractERC20.totalSupply()
        const tx = await contractERC20.approve(wallet.airDropABI.address, res)
        await tx.wait()
        this.isApprove = 1
      } catch (error) {
        console.error(error)
        Tips(error)
      }
      this.approveLoading = false
    },
    // 空投
    async airdropHandler() {
      this.airdropLoading = true
      // TODO 调用空投接口
      try {
        const { address } = contractERC20
        const addrs = this.disList.map((e) => e.addr)
        const amounts = this.disList.map((e) => utils.parseUnits(e.price.toString(), this.ERC20Info.decimals))
        const tx = await wallet.airDropABI.batchSafeTransferFrom(address, addrs, amounts, { value: this.feeAmount })
        await tx.wait()
        ElNotification({
          type: 'success',
          title: 'Success',
          message: 'Airdrop Success'
        })
      } catch (error) {
        console.error(error)
        Tips(error)
      }
      this.airdropLoading = false
    },
    // 空投发送BNB
    async airdropETHHandler() {
      this.airdropLoading = true
      // TODO 调用空投接口
      try {
        const addrs = this.disList.map((e) => e.addr)
        const amounts = this.disList.map((e) => utils.parseEther(e.price.toString()))
        // 初始值是手续费  求和数组
        const totalAmount = amounts.reduce((sum, item) => sum.add(item), this.feeAmount)
        const tx = await wallet.airDropABI.batchSafeSendETH(addrs, amounts, { value: totalAmount })
        await tx.wait()
      } catch (error) {
        console.error(error)
        Tips(error)
      }
      this.airdropLoading = false
    },
    // 授权数据校验
    dataVerify() {
      if (!this.tokenAddr) {
        ElNotification({
          type: 'error',
          title: 'Tips',
          message: 'Please Enter Token Address'
        })
        return false
      }
      if (this.disList.length <= 0) {
        ElNotification({
          type: 'error',
          title: 'Tips',
          message: 'Please Enter Distribution List'
        })
        return false
      }

      let status = true
      for (let i = 0; i < this.disList.length; i++) {
        const { addr, price } = this.disList[i]
        if (addr.substr(0, 2) !== '0x' || addr.length !== 42 || !price) {
          ElNotification({
            type: 'error',
            title: 'Tips',
            message: `The format of line ${i + 1} is wrong, please check the vehicle`
          })
          status = false
          break
        }
      }
      return status
    }
  }
}
</script>

